import refresh from '@/components/common/refresh.vue';
import card from '@/components/common/card.vue';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'

export default {
	components: {
		card,
		refresh,
		uniLoadMore
	},
	data() {
		return {
			status: 'more',
			pageSize: 15,
			pageIndex: 1,
			merchantID:0,
			scrollH:0,
			isPullRefresh: false,
			list: []
		};
	},
	methods: {
		sendSocketMessage(msg) {
			console.log(socketOpen)
			if (socketOpen) {
				uni.sendSocketMessage({
					data: msg
				});
			} else {
				socketMsgQueue.push(msg);
			}
		},
		getDate(dTime) {
			return this.$Time.dateFormat(new Date(dTime), 'yyyy-MM-dd');
		},
		loadList(merchantID, scrollH, callBack){
			this.merchantID = merchantID
			this.scrollH = scrollH
			let url = '/api/merchant/NoticeList';
			let postData = { MerchantID: this.merchantID, pageSize: this.pageSize, pageIndex: this.pageIndex };
			this.$H.post(url, postData).then(res => {
				if (this.isPullRefresh) {
					this.list = res.data;
				} else {
					this.list = [...this.list, ...res.data];
				}
				if (res.data.length + 1 < this.pageSize) {
					this.status = 'noMore';
				} else {
					this.status = 'more';
				}
				if (callBack) {
					callBack()
				}
			});
		},
		showDetail(id) {
			uni.navigateTo({
				url:'../notice/detail?id='+id
			});
		},
		clickLoadMore(e) {
			console.log("bbbbbb")
			if (e.detail.status == 'noMore') {
				return;
			}
			console.log("aaaaaaa")
			this.pageIndex = this.pageIndex + 1;
			this.status = 'loading';
			this.loadList(this.merchantID, this.scrollH)
		},
		//------------------刷新touch监听------------------
		refreshList() {
			this.isPullRefresh = true;
			this.pageIndex = 1;
			this.loadList(this.merchantID, this.scrollH, () => {
				uni.showToast({
					icon: 'none',
					title: '刷新成功'
				});
				//刷新结束调用
				this.$refs.refresh.endAfter(); 
				this.isPullRefresh = false;
			});
		}
	}
};
